import React from "react";
import "./App.css";


const Home = () => {

  function handleClick(e){
    window.location.href = "/About"
  }
    return (
      <div className="App">
      <header className="App-header">
        <h1>안녕하세요!</h1>
        <p>NailStudio에 오신걸 환영합니다!</p>
        <button className="buttontransfer"
         onClick = {handleClick}>NailStudio 소개</button>
        </header>
      </div>
    );
  };

  export default Home;